<template>
  <div id="register">
    <head-vue title="Change Password" goBack="true"></head-vue>
    <ul class="infomation">
      <li>
        Original Password
      </li>
      <li >
        <input type="text" placeholder="please enter your original password" maxlength="16"
        v-model="oldpaswd"
        @focus="borderColor($event)"
        @blur="borderGrey($event)"
        @input="volidate(0)">
      </li>
      <li>
        New Password
      </li>
      <li :class="borderColor? 'active_color':''">
        <input type="password" placeholder="Please enter your new password, 6-16 characters" maxlength="16"
        v-model="newpaswd"
        @focus="borderColor($event)"
        @blur="borderGrey($event)"
        @input="volidate(1)">
      </li>
      <li :class="borderColor? 'active_color':''">
        <input type="password" placeholder="Please enter your new password again, 6-16 characters" maxlength="16"
        v-model="newpaswd2"
        @focus="borderColor($event)"
        @blur="borderGrey($event)" @input="volidate(2)">
      </li>
    </ul>
    <div class="next" @click="next" :class="emailActive?'albeClick' : 'disalbedClick'">
     Finished
    </div>
  </div>
</template>
<script>
import head from '../../components/common/head'
import {
  changePasswordRequest
} from '../../api/method'
export default {
  data () {
    return {
      // emailActive: false, // 输入框是否为空
      oldpaswd: '', // 旧密码
      newpaswd: '',
      newpaswd2: '',
      oldpaswdflag: false,
      newpaswdflag: false,
      newpaswd2flag: false
    }
  },
  components: {
    'head-vue': head
  },
  computed: {
    emailActive () {
      return this.oldpaswdflag && this.newpaswdflag && this.newpaswd2flag
    }
  },
  methods: {
    next () {
      // this.$router.push({name: 'registerNext', params: {email: this.emailVal}})
      if (this.emailActive) {
        changePasswordRequest(this.NAVIGATOR, this, this.newpaswd, this.oldpaswd)
        console.log(this.oldpaswd, this.newpaswd)
        this.$router.go(-1)
      }
      if (this.$root.ISDEBUG) {
        window.errorCodeResponse('0x802506')
      }
    },
    borderColor (e) {
      // 获取焦点线框颜色变色
      let Li = e.target
      while (Li.tagName !== 'LI') {
        Li = Li.parentNode
      }
      Li.style.borderColor = '#01a29a'
      Li.previousElementSibling.style.borderColor = '#01a29a'
    },
    borderGrey (e) {
      // 失去焦点线框颜恢复
      let Li = e.target
      while (Li.tagName !== 'LI') {
        Li = Li.parentNode
      }
      Li.style.borderColor = '#dedede'
      Li.previousElementSibling.style.borderColor = '#dedede'
      // if (this.oldpaswd !== '' && this.newpaswd !== '' && this.newpaswd2 !== '') {
      //   this.emailActive = true
      // } else {
      //   this.emailActive = false
      // }
    },
    volidate (n) {
      console.log(n)
      switch (n) {
        case 0:
          if (this.oldpaswd !== '') {
            this.oldpaswdflag = true
          } else {
            this.oldpaswdflag = false
          }
          break
        case 1:
          if (this.newpaswd.length >= 6) {
            this.newpaswdflag = true
            console.log(this.newpaswdflag)
          } else {
            this.newpaswdflag = false
          }
          break
        case 2:
          if (this.newpaswd2.length >= 6) {
            this.newpaswd2flag = true
            console.log(this.newpaswd2flag)
          } else {
            this.newpaswd2flag = false
          }
          break
        default:
          break
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import "../../styles/mixin.less";
#register{
  .wh(100%, 100%);
  .fs;
  z-index: 999;
  background: white;
  .top{
    .wh(100%, 100px);
    background: @themeColor;
    display: flex;
    padding: 0 30px;
    img{
      .wh(44px, 44px);
      display: block;
      align-self: center;
      margin-right: 128px;
    }
    p{
      line-height: 100px;
      font-size: 41px;
      color: white;
    }
  }
  .infomation{
    .wh(100%, auto);
    margin-top: 30px;
    li{
      border-bottom: 1px solid #dedede;
      padding: 0 30px;
      .wh-l(100%, 110px);
      font-weight: 400;
      display: flex;
      input{
        font-size: 25px;
        background-color: transparent;
        .wh-l(100%, 100%);
        color: black;

      }
      input::-webkit-input-placeholder { /* WebKit browsers */
        color: #dedede;
      }
      input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
        color: #dedede;
      }
      input::-moz-placeholder { /* Mozilla Firefox 19+ */
        color: #dedede;
      }
      input:-ms-input-placeholder { /* Internet Explorer 10+ */
        color: #dedede;
      }
    }
    li:nth-child(1), li:nth-child(3){
    font-size: 32px;
    color: #585858;
    // font-weight: 600;
     input::-webkit-input-placeholder { /* WebKit browsers */
       color: #585858;
     }
    }
  }
  .next{
    .wh-l(520px, 102px);
    border-radius: 10px;
    margin: 0 auto;
    margin-top: 259px;
    color: white;
  }
  .disalbedClick{
    background: #D6D6D6;
  }
  .albeClick{
    background: #13af68;
  }
}
</style>



